@import "ScssHelpers/helpers-all.scss";

.category_list_resourses {
	// padding-top: 8rem;
	// padding-bottom: 9.6rem;

	background-color: $beige_1;
}

.category_list_resourses__list {
	@include flex_block(row, wrap, flex-start, stretch);

	margin: 0 -2.4rem -2.4rem 0;
}

.category_list_resourses__item {
	flex-shrink: 0;
	padding: 0 2.4rem 2.4rem 0;

	@include bp($point_2, min) {
		min-height: 36rem;
		width: 50%;
	}

	@include bp($point_3, $point_4) {
		min-height: 26.4rem;
		width: 50%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		min-height: 21.2rem;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}
