@import "ScssHelpers/helpers-all.scss";

.category_list_link {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;

	&::before,&::after{
		z-index: 2;

		width: 100%;
		height: 100%;

		content:'';
		pointer-events: none;

		@include stretch;
		@include transition_opacity;
	}
	&::before {
		@include bp($point_4, min) {
			background: linear-gradient(5deg, $black 0%, rgba($black, 0) 100%);
			opacity: 0.6;
		}
		@include bp($point_4 - 1) {
			background: linear-gradient(3deg, $black 0%, rgba($black, .05) 100%);
			opacity: 0.5;
		}
	}
	&::after{
		opacity: 0;
		background: $gradient_2;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&::before {
			opacity: 0;
		}
		&::after {
			opacity: 0.8;
		}
	}
}

.category_list_link_img {
	z-index: 1;

	background-color: $black;
	overflow: hidden;

	@include stretch;
}

.category_list_link_img__in {
	@include cover_img;
	@include transition_transform;

	.category_list_link:hover &,
	.category_list_link:focus & {
		transform: scale(1.1);
	}
}

.category_list_link__content {
	position: relative;
	z-index: 3;

	display: flex;
	align-items: flex-end;

	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		.category_list_link--size_mod_2 & {
			@include bp($point_4, min) {
				padding: 3.4rem 4.4rem;
			}

			@include bp($point_4 - 1) {
				padding: 2.4rem;
			}
		}
		&:not(.category_list_link--size_mod_2 &) {
			padding: 2.8rem 3.6rem;
		}
	}
	@include bp($point_3) {
		padding: 1.5rem 1.9rem;
	}
}

.category_list_link__title {
	color: $white;

	@include bp($point_2, min) {
		@at-root {
			h2#{&} {
				letter-spacing: -0.02em;
				line-height: 1;

				@include h2;
			}

			h3#{&} {
				line-height: 1.06;

				@include h3;
			}
		}
	}
	@include bp($point_3) {
		font-size: 2.6rem;
		font-weight: 500;
		letter-spacing: -0.024em;

		br {
			display: none;
		}
	}
}
